@import url('https://fonts.googleapis.com/css2?family=Noto+Sans:ital,wght@0,400;0,700;1,400&display=swap');
@import "rouge-github";


////////////
// COLORS //
////////////
$bg-white: #fff;
$bg-gray: #f6f8fa;
$border-gray: #d0d7de;
$black: #24292f;
$bright-black: #57606a;
$blue: #0969da;
$bright-blue: #218bff;


///////////////////
// MAGIC NUMBERS //
///////////////////
$col-width: 60px;
$col-sep: 20px;
$font-size: 16px;
$line-height: 1.5;
$y-magic-no: $line-height * $font-size;
// If 2*$font-size exceeds $y-magic-no, then adjust header h1 margins.


///////////////////
// GLOBAL STYLES //
///////////////////
* {
  margin: 0;
  padding: 0;
  line-height: $y-magic-no;
}

html, body {
  height: 100%;
}

body {
  background-color: $bg-white;
  padding: 2*$y-magic-no 2*$col-sep;
  font: #{$font-size} / #{$line-height} "Noto Sans", sans-serif;
  color: $black;
}

.wrapper {
  margin: 0 auto;
  padding: 0 0.5*$col-sep;
  position: relative;
  height: auto;
  min-height: 580px; 
  width: 960px;
}

/////////////
// HEADERS //
/////////////
h1, h2, h3, h4 {
  color: $black;
  margin: 0 0 $y-magic-no;
}
h1 {
  font-size: 1.5*$font-size;
}
h2 {
  font-size: 1.25*$font-size;
}
h3 {
  font-size: 1.125*$font-size;
}
// h4 and above not used


////////////////////
// BLOCK ELEMENTS //
////////////////////
p {
  text-align: justify;
}
p, table, pre, dl, iframe {
  margin: 0 0 $y-magic-no;
}
p.post-date {
  color: $bright-black;
}
p img {
  display: block;
}
img {
  max-width:100%;
}
p.caption {
  font-size: 0.75*$font-size;
  line-height: 0.75*$y-magic-no;
  margin: 0 0 0.5*$y-magic-no;
}

hr {
  border: 0;
  background: $bg-gray;
  height: 1px;
  margin: 0 0 #{$y-magic-no - 1px};
}

blockquote {
  background: $bg-gray;
  border-left: 0.2*$col-sep solid $border-gray;
  border-right: $col-sep solid $bg-gray;
  border-radius: 0 0.25*$y-magic-no 0.25*$y-magic-no 0;
  margin: 0 #{-$col-sep} $y-magic-no;
  padding: 0.5*$y-magic-no 0 0.5*$y-magic-no 0.8*$col-sep;
}
blockquote:first-child {
  margin: #{-0.5*$y-magic-no} #{-$col-sep} $y-magic-no #{-$col-sep};
}

blockquote p {
  color: $black;
}
blockquote p:last-child {
  margin: 0;
}


///////////////////
// TEXT ELEMENTS //
///////////////////
a {
  color: $blue;
  line-height: inherit;
  text-decoration: none;
}
a:hover, a:focus {
  color: $bright-blue;
}
h1 a, h2 a, h3 a, h4 a, strong a {
  color: $black;
}
h1 a:hover, h2 a:hover, h3 a:hover, h4 a:hover, strong a:hover,
h1 a:focus, h2 a:focus, h3 a:focus, h4 a:focus, strong a:focus {
  color: $blue;
}
a.button {
  box-shadow: inset 0 0 0 2px $border-gray;
  border-radius: 0.25*$y-magic-no;
  padding: #{0.5*$y-magic-no - 2px} $col-sep;
}

small {
  font-size: 0.75*$font-size;
}


/////////////////////////////
// MONOSPACED/PREFORMATTED //
/////////////////////////////
code, pre {
  font-family: Monaco, Bitstream Vera Sans Mono, Lucida Console, Terminal, Consolas, Liberation Mono, DejaVu Sans Mono, Courier New, monospace;
  color: $black;
}
pre {
  background: $bg-gray;
  border: 1px solid $border-gray;
  border-radius: 0.25*$y-magic-no;
  color: $black;
  font-size: 0.875 * $font-size;
  overflow-x: auto;
  padding: #{0.5*$y-magic-no - 2px} $col-sep;
}


///////////
// LISTS //
///////////
ul, ol {
  margin: 0 0 $y-magic-no $col-sep;
  text-align: justify;
}
li ul, li ol {
  margin: 0 0 $y-magic-no $col-sep;
}

dt {
  clear: left;
  color: $black;
  float: left;
  line-height: $y-magic-no;
  width: 2*$col-width;
}
dd {
  clear: right;
  margin: 0 0 $y-magic-no/2 #{2*$col-width + $col-sep};
}
dd::after {  /* clears proceeding dt(s) ensuring that the next dd will be after next dt */
  content: "";
  display: table;
  clear: left;
} 
dt p, dd p {
  text-align: left;
}
dt:last-child p, dd:last-child p, li:last-child p {
  margin: 0;
}


////////////
// TABLES //
////////////
table {
  width: 100%;
  border-top: 1px solid $border-gray;
  border-collapse: collapse;
  margin: 0 0 $y-magic-no;
}
thead {
  background: $bg-gray;
}
tr {
  border-bottom: 1px solid $border-gray;
  vertical-align: top;
}
th {
  text-align: left;
  padding: #{0.5*$y-magic-no - 1px} 0.5*$y-magic-no;
} 
td {
  text-align: left;
  padding: #{0.5*$y-magic-no - 1px} 0.5*$y-magic-no 0.5*$y-magic-no;
}


////////////
// HEADER //
////////////
header {
  width: 3*$col-width + 2*$col-sep;
  float: left;
  position: fixed;
}

header a, footer a {
  color: $black;
  text-decoration: none;
}
header a:hover, footer a:hover,
header a:focus footer a:focus {
  color: $blue;
}

header h1 {
  font-size: 2*$font-size;
  line-height: max(2*$font-size, $y-magic-no);
  margin: #{$y-magic-no - 2*$font-size} 0 $y-magic-no;
}
img#profile-pic {
  border: 1px solid $border-gray;
  border-radius: 50%;
  width: 156px;
  padding: 5px;
}
ul#nav-bar {
  list-style-type: none;
  margin: 0 0 $y-magic-no;
}
ul#nav-bar li {
  margin: 0 0 $y-magic-no;
}
ul#nav-bar li a {
  font-size: 1.25*$font-size;
  font-weight: bold;
}

section {
  width: 9*$col-width + 8*$col-sep;
  float:right;
  margin:0 0 $y-magic-no;
}

footer {
  float:left;
  position:fixed;
  bottom: $y-magic-no;
  width: 3*$col-width + 2*$col-sep;
  -webkit-font-smoothing:subpixel-antialiased;
}


/////////////////////
// UTILITY CLASSES //
/////////////////////
.bullet-icon {
  margin-right: 0.25em;
  text-indent: 0;
}

.date {
  color: $bright-black;
}

.no-bottom-margin {
  margin-bottom: 0;
}

.column {
  float: left;
  margin: 0 20px 0 0;
  width: 50%;
}
.row::after {
  content: "";
  display: table;
  clear: both;
}


///////////////////////////////////
// NARROW VIEWPORT MODIFICATIONS //
///////////////////////////////////
@media print, screen and (max-width: 960px) {
  body {
    padding: 2*$y-magic-no 2*$y-magic-no $y-magic-no;
  }
  div.wrapper {
    width:auto;
    min-width: 3*$col-width + 2*$col-sep;
  }

  header, section, footer {
    float:none;
    position:static;
    width:auto;
    min-width: 3*$col-width + 2*$col-sep;
    padding: 0;
  }
  
  section {
    border:1px solid $border-gray;
    border-width:1px 0;
    max-width: 9*$col-width + 8*$col-sep;;
    margin:0;
    padding:$y-magic-no 0 0;
  }

  footer {
    margin: $y-magic-no 0 0;
  }
}
